<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import utils from "../libs/utils.js";
import jmoment from "moment-jalaali";

import Multiselect from "@vueform/multiselect";
import DatePicker from "vue3-persian-datepicker";
import "vue3-persian-datepicker/dist/datepicker.min.css";
import Select2 from "vue3-select2-component";
import EditProductDetailVideo from "./EditProductDetailVideo";
import * as Bootstrap from "bootstrap";

export default {
  name: "Sessions",
  setup() {
    return {
      DatePicker,
    };
  },
  created: function () {
    this.jmoment = jmoment;
    this.utils = new utils();
    this.arefApi = new ArefApi(this);
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    Multiselect,
    Select2,
    EditProductDetailVideo,
  },
  data() {
    return {
      sesssionsList: null,
      currentPage: this.$route.params.page ? this.$route.params.page : 1,
      lastPage: null,
      showPreviusSpin: false,
      showNextSpin: false,
      showAllSessions: false,
      formData: {
        days: null,
        from_date: null,
        to_date: null,
        from_time: null,
        to_time: null,
        per_price: null,
        products_id: this.$route.params.productId
          ? this.$route.params.productId
          : null,
        single_purchase: 1,
      },
      singleSessionData: {
        date: null,
        from_time: null,
        to_time: null,
        is_hidden: 0,
        extraordinary: 0,
        price: 0,
        name: null,
        products_id: this.$route.params.productId
          ? this.$route.params.productId
          : null,
        video_session_type: "offline",
        video_link: null,
        single_purchase: 1,
      },
      productName: null,
      productFiles: [],
      errorMulti: null,
      errorSingle: null,
      errorOther: null,
      showSpin: null,
      productList: null,
      selectedProductVideosSessionsList: [],

      otherSessionData: {
        price: 0,
        selectedProductVideosSession: null,
        is_hidden: 0,
        extraordinary: 0,
        name: null,
        products_id: this.$route.params.productId
          ? this.$route.params.productId
          : null,
        single_purchase: 1,
      },
      editProductDetailVideoId: -1,
    };
  },
  methods: {
    async getProductName() {
      try {
        let res = await this.arefApi.getProduct(this.formData.products_id);
        this.productName = res.data.name;
        this.productFiles = res.data.files;
      } catch {
        this.productName = "  ";
      }
    },
    insertMultiSessions() {
      if (!this.validateMultiSessions()) {
        return false;
      }
      this.showSpin = true;

      this.formData.per_price = Number(
        this.formData.per_price.replace(/,/g, "")
      );
      this.formData.from_date = jmoment(
        this.formData.from_date,
        "jYYYY/jM/jD"
      ).format("YYYY-M-D");
      this.formData.to_date = jmoment(
        this.formData.to_date,
        "jYYYY/jM/jD"
      ).format("YYYY-M-D");

      this.formData.single_purchase = Number(this.formData.single_purchase);

      this.arefApi
        .insertVideos(this.formData)
        .then(() => {
          this.utils.showSuccess(this);
          this.getProductVideoSessions(this.currentPage);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          this.showSpin = false;
        });
    },
    monize() {
      this.formData.per_price = this.utils.monize(this.formData.per_price);
      this.singleSessionData.price = this.utils.monize(
        this.singleSessionData.price
      );
      this.otherSessionData.price = this.utils.monize(
        this.otherSessionData.price
      );
    },
    validateMultiSessions() {
      this.errorMulti = null;
      if (!this.formData.days || this.formData.days.length == 0) {
        this.errorMulti = "       ";
        return false;
      }

      if (!this.formData.from_date) {
        this.errorMulti = "     ";
        return false;
      }

      if (!this.formData.to_date) {
        this.errorMulti = "     ";
        return false;
      }

      if (!this.formData.from_time) {
        this.errorMulti = "     ";
        return false;
      }

      if (!this.formData.to_time) {
        this.errorMulti = "     ";
        return false;
      }

      const from_date = jmoment(this.formData.from_date, "jYYYY/jM/jD").unix();
      const to_date = jmoment(this.formData.to_date, "jYYYY/jM/jD").unix();
      if (from_date >= to_date) {
        this.errorMulti = "       ";
        return false;
      }

      const from_time = Date.parse("20 Aug 2021 " + this.formData.from_time);
      const to_time = Date.parse("20 Aug 2021 " + this.formData.to_time);
      if (from_time >= to_time) {
        this.errorMulti = "       ";
        return false;
      }

      return true;
    },
    insertSingleSession() {
      if (!this.validateSingleSessions()) {
        return;
      }
      this.singleSessionData.price = Number(
        this.singleSessionData.price.toString().replace(/,/g, "")
      );
      this.singleSessionData.is_hidden = Number(
        this.singleSessionData.is_hidden
      );
      this.singleSessionData.extraordinary = Number(
        this.singleSessionData.extraordinary
      );
      this.singleSessionData.products_id = Number(
        this.singleSessionData.products_id
      );

      this.singleSessionData.single_purchase = Number(
        this.singleSessionData.single_purchase
      );

      this.singleSessionData.date = jmoment(
        this.singleSessionData.date,
        "jYYYY/jM/jD"
      ).format("YYYY-M-D");

      this.arefApi
        .insertSingleProduct(this.singleSessionData)
        .then(() => {
          this.utils.showSuccess(this);
          this.getProductVideoSessions(this.currentPage);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    validateSingleSessions() {
      this.errorSingle = null;

      if (!this.singleSessionData.date) {
        this.errorSingle = "    ";
        return false;
      }

      if (!this.singleSessionData.from_time) {
        this.errorSingle = "     ";
        return false;
      }

      if (!this.singleSessionData.to_time) {
        this.errorSingle = "     ";
        return false;
      }

      const from_time = Date.parse(
        "20 Aug 2021 " + this.singleSessionData.from_time
      );
      const to_time = Date.parse(
        "20 Aug 2021 " + this.singleSessionData.to_time
      );
      if (from_time >= to_time) {
        this.errorSingle = "       ";
        return false;
      }

      if (
        Number(this.singleSessionData.extraordinary) == 1 &&
        !this.singleSessionData.name
      ) {
        this.errorSingle =
          "            ";
        return false;
      }

      return true;
    },
    insertOtherSession() {
      this.errorOther = null;
      if (!this.otherSessionData.selectedProductVideosSession) {
        this.errorOther = "      ";
        return false;
      }
      this.otherSessionData.price = Number(
        this.otherSessionData.price.toString().replace(/,/g, "")
      );
      this.otherSessionData.is_hidden = Number(this.otherSessionData.is_hidden);
      this.otherSessionData.extraordinary = Number(
        this.otherSessionData.extraordinary
      );
      this.otherSessionData.products_id = Number(
        this.otherSessionData.products_id
      );
      this.otherSessionData.product_detail_videos_id = Number(
        this.otherSessionData.selectedProductVideosSession
      );

      this.otherSessionData.single_purchase = Number(
        this.otherSessionData.single_purchase
      );

      this.arefApi
        .assignVideoToProduct(this.otherSessionData)
        .then(() => {
          this.utils.showSuccess(this);
          this.getProductVideoSessions(this.currentPage);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    async getProductVideoSessions(page) {
      if (!this.formData.products_id) {
        return;
      }
      this.arefApi
        .getProductVideoSessions(
          this.formData.products_id,
          page,
          this.showAllSessions
        )
        .then((sesssionsList) => {
          this.sesssionsList = sesssionsList.data;
          this.currentPage = sesssionsList.meta.current_page;
          this.lastPage = sesssionsList.meta.last_page;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    async getproductList() {
      let out = [];
      try {
        const res = await this.arefApi.getProductsIndex(1, true);
        for (let i in res.data) {
          if (res.data[i].id == this.formData.products_id) {
            continue;
          }
          out.push({
            id: res.data[i].id,
            text: res.data[i].name,
          });
        }
      } catch (e) {
        console.log(e);
      }
      return out;
    },
    getSelectedProductVideosSessionsList({ id }) {
      this.errorOther = null;
      this.selectedProductVideosSessionsList = [];
      this.arefApi
        .getProductVideoSessions(id, 1, true)
        .then((resp) => {
          for (let i in resp.data) {
            this.selectedProductVideosSessionsList.push({
              id: resp.data[i].id,
              text:
                resp.data[i].name +
                " " +
                jmoment(resp.data[i].start_date).format("jYYYY/jM/jD") +
                " " +
                resp.data[i].start_time +
                "-" +
                resp.data[i].end_time,
            });
          }
        })
        .catch(() => {
          this.errorOther = "        ";
        });
    },
    openModal(productDetailVideoId) {
      this.editProductDetailVideoId = productDetailVideoId;
      var myModal = new Bootstrap.Modal(document.getElementById("videoModal"), {
        keyboard: true,
      });
      myModal.toggle();
    },
    nextPage: async function () {
      this.showNextSpin = true;
      this.currentPage++;
      this.$router.push(
        `/admin/sessions/${this.formData.products_id}/${this.currentPage}`
      );
      await this.getProductVideoSessions(this.currentPage);
      this.showNextSpin = false;
    },
    previusPage: async function () {
      this.showPreviusSpin = true;
      this.currentPage--;
      this.$router.push(
        `/admin/sessions/${this.formData.products_id}/${this.currentPage}`
      );
      await this.getProductVideoSessions(this.currentPage);
      this.showPreviusSpin = false;
    },
    changePage: function (event) {
      this.$router.push(
        `/admin/sessions/${this.formData.products_id}/${event.target.value}`
      );
      this.getProductVideoSessions(event.target.value);
    },
    deleteItem: function (productDetailVideoId) {
      this.$swal
        .fire({
          title: " ",
          text: "   ",
          icon: "warning",
          showCancelButton: true,
          cancelButtonText: "",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "   !",
        })
        .then((value) => {
          if (value.isConfirmed) {
            this.arefApi
              .deleteProductDetailVideo(productDetailVideoId)
              .then(() => {
                this.$swal
                  .fire({
                    title: ``,
                    text: `    `,
                    icon: "success",
                  })
                  .then(() => {
                    this.getProductVideoSessions(this.currentPage);
                  });
              })
              .catch((err) => {
                this.utils.showErrors(this, err);
              });
          }
        });
    },
  },
  async mounted() {
    if (!this.formData.products_id) {
      this.$swal
        .fire({
          title: "!",
          text: `  `,
          icon: "error",
          confirmButtonText: " ",
        })
        .then(() => {
          return this.$router.push({ name: "ListProducts" });
        });
    }
    this.getProductName();
    this.getProductVideoSessions(this.currentPage);
    this.productList = await this.getproductList();
  },
};
</script>
